import Vue from 'vue';
import i18n from '../../../i18n';
import { faChartLine, faPlus, faHashtag, faRocket, faCertificate, faSearch } from '@fortawesome/free-solid-svg-icons';
import { faBookmark, faCommentAlt } from '@fortawesome/free-regular-svg-icons';
export default Vue.extend({
    i18n: i18n('common/views/pages/explore.vue'),
    props: {
        tag: {
            type: String,
            required: false
        }
    },
    inject: {
        inNakedDeckColumn: {
            default: false
        }
    },
    data() {
        return {
            pinnedUsers: { endpoint: 'pinned-users' },
            verifiedUsers: { endpoint: 'users', limit: 10, params: {
                    state: 'verified',
                    origin: 'local',
                    sort: '+follower',
                } },
            popularUsers: { endpoint: 'users', limit: 10, params: {
                    state: 'alive',
                    origin: 'local',
                    sort: '+follower',
                } },
            recentlyUpdatedUsers: { endpoint: 'users', limit: 10, params: {
                    origin: 'local',
                    sort: '+updatedAt',
                } },
            recentlyRegisteredUsers: { endpoint: 'users', limit: 10, params: {
                    origin: 'local',
                    state: 'alive',
                    sort: '+createdAt',
                } },
            popularUsersF: { endpoint: 'users', limit: 10, params: {
                    state: 'alive',
                    origin: 'remote',
                    sort: '+follower',
                } },
            recentlyUpdatedUsersF: { endpoint: 'users', limit: 10, params: {
                    origin: 'combined',
                    sort: '+updatedAt',
                } },
            recentlyRegisteredUsersF: { endpoint: 'users', limit: 10, params: {
                    origin: 'combined',
                    sort: '+createdAt',
                } },
            tagsLocal: [],
            tagsRemote: [],
            stats: null,
            query: null,
            meta: null,
            num: Vue.filter('number'),
            faBookmark, faChartLine, faCommentAlt, faPlus, faHashtag, faRocket, faCertificate, faSearch
        };
    },
    computed: {
        tagUsers() {
            return {
                endpoint: 'hashtags/users',
                limit: 30,
                params: {
                    tag: this.tag,
                    origin: 'combined',
                    sort: '+follower',
                }
            };
        },
        foundUsers() {
            return {
                endpoint: 'users/search',
                limit: 30,
                params: {
                    query: this.query,
                }
            };
        },
    },
    watch: {
        tag() {
            if (this.$refs.tags)
                this.$refs.tags.toggleContent(this.tag == null);
        }
    },
    created() {
        this.$emit('init', {
            title: this.$t('@.explore'),
            icon: faHashtag
        });
        this.$root.api('hashtags/list', {
            sort: '+attachedLocalUsers',
            attachedToLocalUserOnly: true,
            limit: 30
        }).then(tags => {
            this.tagsLocal = tags;
        });
        this.$root.api('hashtags/list', {
            sort: '+attachedRemoteUsers',
            attachedToRemoteUserOnly: true,
            limit: 30
        }).then(tags => {
            this.tagsRemote = tags;
        });
        this.$root.api('stats').then(stats => {
            this.stats = stats;
        });
        this.$root.getMeta().then(meta => {
            this.meta = meta;
        });
    },
    mounted() {
        document.title = this.$root.instanceName;
    },
});
